import { makeAutoObservable } from 'mobx'
import type Fraction from 'fraction.js'
import { getNextOperation, getNextModifier, getNextEffect } from './next'
import { type Action, type Bed, type Modifier, type Operation, type Effect, operationOrder, type SolutionStep } from './types'
import { SolverStateMachine } from './solver'

export const groupActionsByType = (actions: Action[]): Map<Bed, Action[]> => {
	const result = new Map<Bed, Action[]>()
	result.set("single1", [])
	result.set("treble", [])
	result.set("single2", [])
	result.set("double", [])
	for (const action of actions) {
		const bed = result.get(action.type)
		if (bed) {
			bed.push(action)
		}
	}
	return result
}

export type OperationAction = {
	op: Operation
	score: number
	type: Bed
	third: boolean
}

export type ScoreAndModifier = {
	score: number
	modifier: Modifier
}

export type Solution = {
	score?: Fraction
	steps: SolutionStep[]
}

export class Model {

	private readonly _innerSingles = new Map<number, OperationAction>()
	private readonly _trebles = new Map<number, OperationAction>()
	private readonly _outerSingles = new Map<number, OperationAction>()
	private readonly _doubles = new Map<number, OperationAction>()

	private readonly _modifiers = new Map<number, Modifier>()

	// 0 = first effect, 1 = second, 2 = last
	// so this is inner -> outer
	private readonly _effects: (Effect | undefined)[] = []

	private _effectOp?: Operation

	constructor() {
		makeAutoObservable(this)
	}

	/**
	 * Activate a scoring segment.
	 *
	 * @param score score sector to activate
	 * @param type type (bed) within the sector
	 * @param alt true for a normal activation (short press/click), false for an alternate activation(long press/click)
	 */
	activateScore(score: number, type: Bed, alt: boolean) {
		if (['single1', 'treble', 'single2', 'double'].includes(type)) {
			const map = this.getScoreMap(type)
			const currentValue = map.get(score)
			if (currentValue) {
				if (!alt) {
					// Normal activation, cycle through the operations...
					const nextOp = getNextOperation(currentValue.op)
					if (nextOp) {
						currentValue.op = nextOp
						map.delete(score)
						map.set(score, currentValue)
					} else {
						map.delete(score) // could optimise this dupe
					}
				} else {
					// Alternate activation, toggle 1/3 mode
					currentValue.third = !currentValue.third
					map.delete(score) // again optimise?
					map.set(score, currentValue)
				}
			} else {
				const newValue: OperationAction = {
					op: 'add',
					score,
					type,
					third: alt
				}
				map.set(score, newValue)
			}
			return
		}

		if ('modifier' === type) {
			const currentValue = this._modifiers.get(score)
			if (currentValue) {
				const nextMod = getNextModifier(currentValue)
				if (nextMod) {
					this._modifiers.set(score, nextMod)
				} else {
					this._modifiers.delete(score)
				}
			} else {
				const newMod = 'double'
				this._modifiers.set(score, newMod)
			}
			return
		}
		throw new Error()
	}

	activateEffectOp() {
		this._effectOp = getNextOperation(this._effectOp)
	}

	activateEffect(slot: number) {
		const currentValue = this._effects[slot]
		this._effects[slot] = getNextEffect(currentValue)
	}

	reset() {
		this._innerSingles.clear()
		this._trebles.clear()
		this._outerSingles.clear()
		this._doubles.clear()
		this._modifiers.clear()
		this._effects.length = 0
		this._effectOp = undefined
	}

	get innerSingles(): OperationAction[] {
		return [...this._innerSingles.values()]
	}

	get trebles(): OperationAction[] {
		return [...this._trebles.values()]
	}

	get outerSingles(): OperationAction[] {
		return [...this._outerSingles.values()]
	}

	get doubles(): OperationAction[] {
		return [...this._doubles.values()]
	}

	get modifiers(): ScoreAndModifier[] {
		return Array.from(this._modifiers, ([score, modifier]) => ({ score, modifier }))
	}

	get effects(): (Effect | undefined)[] {
		return [...this._effects].filter(effect => effect !== undefined)
	}

	get effectOperation(): Operation | undefined {
		return this._effectOp
	}

	getScoreData(score: number, type: Bed): OperationAction | undefined {
		return this.getScoreMap(type).get(score)
	}

	get solution(): Solution {
		const allActions: OperationAction[] = [
			...[...this._innerSingles.values()],
			...[...this._trebles.values()],
			...[...this._outerSingles.values()],
			...[...this._doubles.values()],
		]
		return new SolverStateMachine().solve(allActions, this._modifiers, this.effectOperation, this._effects)
	}

	getEffect(slot: number) {
		return this._effects[slot]
	}

	private getScoreMap(type: Bed): Map<number, OperationAction> {
		switch (type) {
			case 'single1':
				return this._innerSingles
			case 'treble':
				return this._trebles
			case 'single2':
				return this._outerSingles
			case 'double':
				return this._doubles
			default:
				throw new Error(`Not a score map: ${type}`)
		}
	}
}

export const MODEL = new Model()
