import type { PropsWithChildren } from 'react'
import { useConfig } from '../../context/ConfigContext'
import { exhaustiveCheck } from '../../lib/exhaustive'

const wavyLinePathData = (radius: number, baseY: number, level: number) => {
	// The line starts on the left edge of the circle, with control points half way between the edge and the circle center line
	const centerX = 0
	const centerY = 0
	const startX = -radius
	const control1X = -radius / 2
	const midX = centerX
	const control2X = radius / 2
	const endX = radius
	const baselineY = centerY - radius * (1 - baseY)
	const dipY = baselineY + (5 / 32) * radius
	const riseY = baselineY - (5 / 32) * radius
	const d = [
		`M${startX} ${baselineY}`,
		`Q${control1X} ${dipY}, ${midX} ${baselineY}`,
		`Q${control2X} ${riseY}, ${endX} ${baselineY}`,
	].join(' ')
	return d
}

type NearestEffectProps = PropsWithChildren<{
	variant: 1 | 10 | 100
	level: 1 | 2
}>

export const NearestEffect = ({ variant, level, children }: NearestEffectProps) => {
	const config = useConfig()

	const strokeWidth = 1

	let pos1: number[]
	let pos2: number[]
	let pos3: number[]
	switch (level) {
		case 1:
			pos1 = [5, 23]
			pos2 = [8, 20]
			pos3 = [2, 26]
			break
		case 2:
			pos1 = [9, 19]
			pos2 = [11, 17]
			pos3 = [7, 21]
			break
		default:
			return exhaustiveCheck(level)
	}

	// THe numbers in the factional part are because the lines are drawn to some fraction of 64/32/whatever ?
	return (
		<>
			<path d={wavyLinePathData(config.bull, pos1[0] / config.bull, level)} stroke='black' strokeWidth={strokeWidth} fill='none' />
			<path d={wavyLinePathData(config.bull, pos1[1] / config.bull, level)} stroke='black' strokeWidth={strokeWidth} fill='none' />
			{variant > 1 && (
				<>
					<path
						d={wavyLinePathData(config.bull, pos2[0] / config.bull, level)}
						stroke='black'
						strokeWidth={strokeWidth}
						fill='none'
					/>
					<path
						d={wavyLinePathData(config.bull, pos2[1] / config.bull, level)}
						stroke='black'
						strokeWidth={strokeWidth}
						fill='none'
					/>
				</>
			)}
			{variant > 10 && (
				<>
					<path
						d={wavyLinePathData(config.bull, pos3[0] / config.bull, level)}
						stroke='black'
						strokeWidth={strokeWidth}
						fill='none'
					/>
					<path
						d={wavyLinePathData(config.bull, pos3[1] / config.bull, level)}
						stroke='black'
						strokeWidth={strokeWidth}
						fill='none'
					/>
				</>
			)}
			{children}
		</>
	)
}

export const NearestEffectSvg = ({ variant }: NearestEffectProps) => {
	const config = useConfig()

	const bull = config.bull
	const viewBox = `-${bull} -${bull} ${bull * 2} ${bull * 2}`

	return (
		<svg width='64' height='64' viewBox={viewBox} preserveAspectRatio='xMidYMid meet'>
			<path d={wavyLinePathData(bull, 7 / bull, 1)} stroke='black' strokeWidth='2' fill='none' />
			<path d={wavyLinePathData(bull, 21 / bull, 1)} stroke='black' strokeWidth='2' fill='none' />
			{variant > 1 && (
				<>
					<path d={wavyLinePathData(bull, 10 / bull, 1)} stroke='black' strokeWidth='2' fill='none' />
					<path d={wavyLinePathData(bull, 18 / bull, 1)} stroke='black' strokeWidth='2' fill='none' />
				</>
			)}
			{variant > 10 && (
				<>
					<path d={wavyLinePathData(bull, 4 / bull, 1)} stroke='black' strokeWidth='2' fill='none' />
					<path d={wavyLinePathData(bull, 24 / bull, 1)} stroke='black' strokeWidth='2' fill='none' />
				</>
			)}
		</svg>
	)
}
