import type { BoardConfig } from '../../board/config'
import { polarToCartesian } from '../../lib/coords'

type SwtichModifierOverlayProps = {
	startAngle: number
	config: BoardConfig
}

export const SwitchModifierOverlay = ({ config, startAngle }: SwtichModifierOverlayProps) => {
	const segmentAngle = 360 / config.scores.length
	const circleRadius = config.radius * 0.043
	const midRadius = (config.modifierInner + config.modifierOuter) / 2

	const diamondPos = polarToCartesian(0, 0, midRadius, startAngle + segmentAngle / 2)

	const angle = startAngle + segmentAngle / 2 + 180

	return (
		<g>
			<polygon
				className='modifier'
				points={getDiamondPoints(diamondPos.x, diamondPos.y, circleRadius)}
				transform={`rotate(${angle}, ${diamondPos.x}, ${diamondPos.y})`}
			/>
		</g>
	)
}

const getDiamondPoints = (cx: number, cy: number, radius: number): string => {
	const points = [
		{ x: 0, y: -radius - 2 },
		{ x: radius, y: 0 },
		{ x: 0, y: radius + 2},
		{ x: -radius, y: 0 },
	]
	return points.map((point) => `${cx + point.x},${cy + point.y}`).join(' ')
}
