import { useConfig } from '../../context/ConfigContext'

export const NoEffectSvg = () => {
	const config = useConfig()
	const bull = config.bull
	const viewBox = `-${bull} -${bull} ${bull * 2} ${bull * 2}`
	return <svg width='64' height='64' viewBox={viewBox} preserveAspectRatio='xMidYMid meet' />
}
