import type { ScoreAndModifier } from '../../model/model'
import { useConfig } from '../../context/ConfigContext'
import { exhaustiveCheck } from '../../lib/exhaustive'
import { SwitchModifierOverlay } from './SwitchModifierOverlay'
import { DoubleModifierOverlay } from './DoubleModifierOverlay'
import { HalfModifierOverlay } from './HalfModifierOverlay'
import { IgnoreModifierOverlay } from './IgnoreModifierOverlay'

type ModifierOverlayProps = {
    modifier: ScoreAndModifier
}

export const ModifierOverlay = ({ modifier }: ModifierOverlayProps) => {
    const config = useConfig()

    const index = config.scores.findIndex((score) => score === modifier.score)
	if (index === -1) {
		throw new Error('Bug')
	}

	const arcAngle = 360 / config.scores.length
	const arcOffset = -arcAngle / 2
	const startAngle = (index * arcAngle) + arcOffset
	const endAngle = ((index + 1) * arcAngle) + arcOffset

    const type = modifier.modifier
    switch (type) {
        case 'double':
            return <DoubleModifierOverlay startAngle={startAngle} config={config} />
        case 'half':
            return <HalfModifierOverlay startAngle={startAngle} endAngle={endAngle} config={config} />
        case 'ignore':
            return <IgnoreModifierOverlay startAngle={startAngle} endAngle={endAngle} config={config} />
        case 'switch':
            return <SwitchModifierOverlay startAngle={startAngle} config={config} />
        default:
            return exhaustiveCheck(type)
    }
}
