import type { BoardConfig } from '../../board/config'
import { polarToCartesian } from '../../lib/coords'

type DoubleModifierOverlayProps = {
	startAngle: number
	config: BoardConfig
}

export const DoubleModifierOverlay = ({ config, startAngle }: DoubleModifierOverlayProps) => {
	const segmentAngle = 360 / config.scores.length
	const circleRadius = config.radius * 0.043
	const midRadius = (config.modifierInner + config.modifierOuter) / 2
	const angleOffsetDeg = segmentAngle / 4.7

	const circle1Pos = polarToCartesian(0, 0, midRadius, startAngle + segmentAngle / 2 - angleOffsetDeg)
	const circle2Pos = polarToCartesian(0, 0, midRadius, startAngle + segmentAngle / 2 + angleOffsetDeg)

	const angle1 = startAngle + segmentAngle / 2 - angleOffsetDeg + 22.5
	const angle2 = startAngle + segmentAngle / 2 + angleOffsetDeg + 22.5

	return (
		<g>
			<polygon
				className='modifier'
				points={getOctagonPoints(circle1Pos.x, circle1Pos.y, circleRadius)}
				transform={`rotate(${angle1}, ${circle1Pos.x}, ${circle1Pos.y})`}
			/>
			<polygon
				className='modifier'
				points={getOctagonPoints(circle2Pos.x, circle2Pos.y, circleRadius)}
				transform={`rotate(${angle2}, ${circle2Pos.x}, ${circle2Pos.y})`}
			/>
		</g>
	)
}

const getOctagonPoints = (cx: number, cy: number, radius: number): string => {
	const points: string[] = []
	for (let i = 0; i < 8; i++) {
		const angle = (Math.PI / 4) * i
		const x = cx + radius * Math.cos(angle)
		const y = cy + radius * Math.sin(angle)
		points.push(`${x.toFixed(2)},${y.toFixed(2)}`)
	}
	return points.join(' ')
}
