import { observer } from 'mobx-react-lite'
import { EffectOverlays } from './effect/EffectOverlays'
import { ModifierOverlays } from './modifier/ModifierOverlays'
import { ScoreOverlays } from './score/ScoreOverlays'
import { useConfig } from '../context/ConfigContext'
import { useModel } from '../context/ModelContext'
import { SolutionOverlay } from './solution/SolutionOverlay'

export const Overlays = observer(() => {
	const config = useConfig()

	const model = useModel()

	const size = (config.radius + config.edgeWidth / 2 + 2) * 2

	return (
		<svg className='overlay' viewBox={`-${size / 2} -${size / 2} ${size} ${size}`}>
			<ScoreOverlays scores={model.innerSingles} type='single1' />
			<ScoreOverlays scores={model.trebles} type='treble' />
			<ScoreOverlays scores={model.outerSingles} type='single2' />
			<ScoreOverlays scores={model.doubles} type='double' />
			<ModifierOverlays modifiers={model.modifiers} />
			<EffectOverlays effects={model.effects} operation={model.effectOperation} />
			<SolutionOverlay solution={model.solution.score} />

			<circle cx={0} cy={0} r={config.scoreInner} className='numbers-ring-border' />
			<circle cx={0} cy={0} r={config.scoreOuter} className='numbers-ring-border' />

		</svg>
	)
})
