import type Fraction from 'fraction.js'
import { AnnularSector } from '../../board/AnnularSector'
import { useConfig } from '../../context/ConfigContext'

type SolutionOverlayProps = {
    solution?: Fraction
}

export const SolutionOverlay = ({ solution }: SolutionOverlayProps ) => {
    const config = useConfig()

    if (!solution) {
        return null
    }

    return (
        <AnnularSector
            className='result'
            config={config}
            value={solution.valueOf()}
            type='score'
        />
    )
}
