import { useRef, useCallback } from 'react'

type UseLongPressProps = {
	onPress: () => void
	onLongPress: () => void
	longPressDelay?: number
}

type LongPressHook = {
	onPointerDown: (event: React.PointerEvent) => void
	onPointerUp: (event: React.PointerEvent) => void
	onPointerCancel: (event: React.PointerEvent) => void
	onPointerLeave: (event: React.PointerEvent) => void
}

export const useLongPress = ({ onPress, onLongPress, longPressDelay = 500 }: UseLongPressProps): LongPressHook => {
	const timeoutRef = useRef<number | null>(null)
	const isLongPressRef = useRef<boolean>(false)
	const inputTypeRef = useRef<'mouse' | 'touch' | 'pen' | null>(null)

	const startPress = useCallback(() => {
		isLongPressRef.current = false
		timeoutRef.current = window.setTimeout(() => {
			isLongPressRef.current = true
			onLongPress()
			timeoutRef.current = null
		}, longPressDelay)
	}, [onLongPress, longPressDelay])

	const endPress = useCallback(() => {
		if (timeoutRef.current) {
			window.clearTimeout(timeoutRef.current)
			timeoutRef.current = null
			if (!isLongPressRef.current) {
				onPress()
			}
		}
	}, [onPress])

	const cancelPress = useCallback(() => {
		if (timeoutRef.current) {
			window.clearTimeout(timeoutRef.current)
			timeoutRef.current = null
		}
	}, [])

	const onPointerDown = useCallback(
		(event: React.PointerEvent) => {
			event.preventDefault()
			if (inputTypeRef.current && inputTypeRef.current !== event.pointerType) {
				return
			}
			inputTypeRef.current = event.pointerType as 'mouse' | 'touch' | 'pen'
			startPress()
		},
		[startPress],
	)

	const onPointerUp = useCallback(
		(event: React.PointerEvent) => {
			if (inputTypeRef.current !== event.pointerType) {
				return
			}
			endPress()
			inputTypeRef.current = null
		},
		[endPress],
	)

	const onPointerCancel = useCallback(
		(event: React.PointerEvent) => {
			if (inputTypeRef.current !== event.pointerType) {
				return
			}
			cancelPress()
			inputTypeRef.current = null
		},
		[cancelPress],
	)

	const onPointerLeave = useCallback(
		(event: React.PointerEvent) => {
			if (inputTypeRef.current !== event.pointerType) {
				return
			}
			cancelPress()
			inputTypeRef.current = null
		},
		[cancelPress],
	)

	return {
		onPointerDown,
		onPointerUp,
		onPointerCancel,
		onPointerLeave,
	}
}
