import type { OperationAction, Solution } from './model'
import type { Effect, Bed, Operation, Modifier, SolutionStep } from './types'
import Fraction from 'fraction.js'
import { exhaustiveCheck } from '../lib/exhaustive'
import { switchDigits } from '../lib/switch-digits'
import { roundToNearest } from '../lib/round-to-nearest'
import { sorter } from './sorter'

type ActionResult = {
operation: Operation
    modifier?: Modifier
    effectiveScore?: Fraction
    result: Fraction
    description: string
}

type EffectsResult = {
	results: EffectResult[]
	result: Fraction
}

type EffectResult = {
	effect: Effect
	result: Fraction
	description: string
}

enum SolverState {
	READY = 'Ready',
	NEXT_ACTION = 'Next Action',
	NO_MORE_ACTIONS = 'No More Actions',
	OPERATION_CHANGED = 'Operation Changed',
	STAGE_CHANGED = 'Stage Changed',
	CHECK_EFFECTS = 'Check Effects',
	NEW_OPERATION_TYPE = 'New Operation Type',
	NEW_STAGE = 'New Stage',
	PROCESS_ACTION = 'Process Action',
	PROCESS_EFFECTS = 'Process Effects',
	CONTINUE_ACTION = 'Continue Action',
	DONE = 'Done',
}

export class SolverStateMachine {

	/**
	 * Solve the puzzle.
	 *
	 * Note that the supplied actions are already expected to be in the required sort order.
	 *
	 * @see {@link ./sorter.ts#sorter}
	 *
	 * @param sortedActions
	 * @param modifiers
	 * @param effectOperation
	 * @param effects
	 * @returns
	 */
	solve(
		actions: OperationAction[],
		modifiers: Map<number, Modifier>,
		effectOperation: Operation | undefined,
		effects: (Effect | undefined)[],
	): Solution {
		let result = new Fraction(0)

        const steps: SolutionStep[] = []

		let actionIndex = 0

		let currentStage: Exclude<Bed, 'score' | 'modifier'> | null = null
		let currentAction: OperationAction | null = null
		let nextAction: OperationAction | null = null
		let lastOperation: Operation | null = null

		const sortedActions = actions.sort(sorter(effectOperation))

		let state = SolverState.READY
		while (state !== SolverState.DONE) {
			switch (state) {
				case SolverState.READY:
					currentStage = 'single1'
					currentAction = sortedActions[0]
					if (currentAction) {
						state = SolverState.NEXT_ACTION
						break
					}
					state = SolverState.DONE
					break

				case SolverState.NEXT_ACTION: {
					nextAction = sortedActions[actionIndex] ?? null
					if (!nextAction) {
						state = SolverState.NO_MORE_ACTIONS
						break
					}
					if (lastOperation !== null && lastOperation !== nextAction.op) {
						state = SolverState.OPERATION_CHANGED
						break
					}
					if (currentStage !== nextAction.type) {
						state = SolverState.STAGE_CHANGED
						break
					}
					// we should go to CONTINUE_ACTION here since the code is dupe? next_action is like CHECK_EFFECTS_APPLY or something, then CONTIONUE can be PROCESS
					currentAction = nextAction
					lastOperation = nextAction.op
					// nextAction = null
					actionIndex++
					state = SolverState.PROCESS_ACTION
					break
				}

				case SolverState.CONTINUE_ACTION:
					if (!nextAction) {
						throw new Error('Bug: no nextAction')
					}

					currentAction = nextAction
					lastOperation = nextAction.op
					// nextAction = null
					actionIndex++
					state = SolverState.PROCESS_ACTION
					break

				case SolverState.NO_MORE_ACTIONS:
					state = SolverState.CHECK_EFFECTS
					break

				case SolverState.OPERATION_CHANGED:
					state = SolverState.CHECK_EFFECTS
					break

				case SolverState.STAGE_CHANGED: {
					if (effectOperation !== 'div') {
						state = SolverState.CHECK_EFFECTS
						break
					}
					const lookaheadAction = sortedActions[actionIndex]
					if (lookaheadAction?.op === 'div') {
						state = SolverState.CONTINUE_ACTION
						break
					}
					state = SolverState.CHECK_EFFECTS
					break
				}

				case SolverState.CHECK_EFFECTS:
					if (currentAction === null) {
						throw new Error()
					}

					if (effectOperation === lastOperation) {
						const effectsResult = this.processEffects(currentAction, effectOperation, effects, result)
						for (const effectResult of effectsResult.results) {
							steps.push({
								type: 'effect',
								operation: currentAction.op,
								effect: effectResult.effect,
								effectiveScore: new Fraction(0),
								stage: currentAction.type,
								display: effectResult.description,
								result: effectResult.result
							})
						}
						result = effectsResult.result
					}

					if (nextAction) {
						// Either, or both, of operation and stage may have changed
						lastOperation = nextAction.op
						currentStage = nextAction.type as typeof currentStage // Unfortunate casting
						state = SolverState.NEXT_ACTION
						break
					}
					state = SolverState.DONE
					break

				case SolverState.PROCESS_ACTION: {
					if (!currentAction) {
						throw new Error('Bug: no currentAction')
					}
                    // ideally this will return the step
                    const actionResult = this.processAction(currentAction, modifiers, result)
                    steps.push({
                        type: 'score',
                        operation: actionResult.operation,
                        modifier: actionResult.modifier,
                        score: currentAction.score,
                        effectiveScore: actionResult.effectiveScore,
                        stage: currentAction.type,
                        display: actionResult.description,
                        result: actionResult.result,

                    })
					result = actionResult.result
					state = SolverState.NEXT_ACTION
					break
				}

				default:
					return exhaustiveCheck(state)
			}
		}

		return {
			steps,
			score: result,
		}
	}

    // The display string should actually be UI component only, not in here (it's only used for debugging)

	private processAction(action: OperationAction, modifiers: Map<number, Modifier>, baseResult: Fraction): ActionResult {
        const modifier = modifiers.get(action.score)
		const effectiveScore = this.getModifierScore(action, modifier)
		const operation = action.op
        let result: Fraction
        let description: string
        if (effectiveScore) {
            switch (operation) {
                case 'add':
                    result = baseResult.add(effectiveScore)
                    description = `+${effectiveScore.toFraction()}`
                    break
                case 'sub':
                    result = baseResult.sub(effectiveScore)
                    description = `−${effectiveScore.toFraction()}`
                    break
                case 'mul':
                    result = baseResult.mul(effectiveScore)
                    description = `×${effectiveScore.toFraction()}`
                    break
                case 'div':
                    result = baseResult.div(effectiveScore)
                    description = `÷${effectiveScore.toFraction()}`
                    break
                default:
                    return exhaustiveCheck(operation)
            }
        } else {
            result = baseResult
            description = `x̶${action.score.toString()}`
        }
        return {
            operation,
            modifier,
            effectiveScore,
            result,
            description
        }
    }

	private processEffects(
		action: OperationAction,
		effectOperation: string,
		effects: (Effect | undefined)[],
		baseResult: Fraction,
	): EffectsResult {
		const results: EffectResult[] = []

		if (effectOperation !== action.op) {
			return {
				results,
				result: baseResult
			}
		}
		let result = baseResult
        for (const effect of effects) {
            if (effect !== undefined) {
                const effectScore = this.getEffectScore(result, effect)
                result = effectScore.result
				results.push(effectScore)
            }
        }
		return {
			results,
			result
		}
	}

	/**
	 * Apply a modifier to a score.
	 *
	 * A modifier is applied to each score before the operation is applied, so for example if the
	 * score was 16, and the modifieri was 'double', then the modified score becomes 32 and that
	 * value is used subsequently when applying the operation.
	 *
	 * @param action current action
	 * @param modifier modifier for the score in the action
	 * @returns new score after applying a modifier, if one exists, or undefined if the modifier is 'ignore'
	 */
	private getModifierScore(action: OperationAction, modifier: Modifier | undefined): Fraction | undefined {
		let effectiveScore: Fraction | undefined = new Fraction(action.score)
		switch (modifier) {
			case 'double':
				effectiveScore = effectiveScore.mul(2)
				break
			case 'half':
				effectiveScore = effectiveScore.div(2)
				break
			case 'ignore':
				effectiveScore = undefined
				break
			case 'switch':
				effectiveScore = switchDigits(effectiveScore)
				break
			case undefined:
				break
			default:
				return exhaustiveCheck(modifier)
		}
		if (action.third) {
			switch (modifier) {
				case 'double':
				case 'half':
				case 'switch':
				case undefined:
					effectiveScore = effectiveScore?.div(3)
					break
				default:
					break
			}
		}
		return effectiveScore
	}

	/**
	 * Apply an effect to the current result.
	 *
	 * An effect is applied after processing of a set of like actions is finished, and serves to
	 * adjust the final result of those actions - for example various adds, substractions,
	 * multiplications and divisions are processed, and then the effect, such as squaring the
	 * result, is applied to that previous result.
	 *
	 * @param baseResult current result
	 * @param effect effect to apply
	 * @returns new result with the effect applied
	 */
	private getEffectScore(baseResult: Fraction, effect: Effect): EffectResult {
		switch (effect) {
			case 'square':
				return {
					effect,
					result: baseResult.mul(baseResult),
					description: `${baseResult.toFraction()}²`,
				}
			case 'switch':
				return {
					effect,
					result: switchDigits(baseResult),
					description: `${baseResult.toFraction()} → ${switchDigits(baseResult).toFraction()}`,
				}
			case 'nearest1':
				return {
					effect,
					result: roundToNearest(baseResult, 1),
					description: `⟦${baseResult.toFraction()}⟧_1`,
				}
			case 'nearest10':
				return {
					effect,
					result: roundToNearest(baseResult, 10),
					description: `⟦${baseResult.toFraction()}⟧_10`,
				}
			case 'nearest100':
				return {
					effect,
					result: roundToNearest(baseResult, 100),
					description: `⟦${baseResult.toFraction()}⟧_100`,
				}
			default:
				return exhaustiveCheck(effect)
		}
	}
}
