import type { ScoreAndModifier } from '../../model/model'
import { ModifierOverlay } from './ModifierOverlay'

type ModifierOverlaysProps = {
	modifiers: ScoreAndModifier[]
}

export const ModifierOverlays = ({ modifiers }: ModifierOverlaysProps) => {
    return (
        <>
            {modifiers.map(modifier => (
                <ModifierOverlay key={modifier.score} modifier={modifier} />
            ))}
        </>
    )

}
