import { AnnularSector } from '../../board/AnnularSector'
import { useConfig } from '../../context/ConfigContext'
import type { Action, Bed } from '../../model/types'

type ScoreOverlaysProps = {
	scores: Action[]
	type: Bed
}

export const ScoreOverlays = ({ scores, type }: ScoreOverlaysProps) => {
	const config = useConfig()

	return (
		<>
			{scores.map(score => (
				<AnnularSector
					key={`${score.score}-${type}`}
					className={score.op}
					config={config}
					value={score.score}
					type={score.type}
					third={score.third}
				/>
			))}
		</>
	)
}
