import { createContext, type PropsWithChildren, useContext } from 'react'
import type { Model } from '../model/model'

type ModelProviderProps = PropsWithChildren<{
    model: Model
}>

const ModelContext = createContext<ModelProviderProps | null>(null)

export const ModelProvider = ({ model, children }: ModelProviderProps) => {
    return <ModelContext.Provider value={{ model }}>{children}</ModelContext.Provider>
}

export const useModel = (): Model => {
    const context = useContext(ModelContext)
    if (!context) {
        throw new Error('useModel must be used within a ModelProvider')
    }
    return context.model
}
