import type { ReactNode } from 'react'
import type { Effect } from '../model/types'
import { SquareEffectSvg } from '../overlays/effect/SquareEffect'
import { SwitchEffectSvg } from '../overlays/effect/SwitchEffect'
import { NearestEffectSvg } from '../overlays/effect/NearestEffect'
import { observer } from 'mobx-react-lite'
import { NoEffectSvg } from '../overlays/effect/NoEffect'
import { exhaustiveCheck } from '../lib/exhaustive'
import { useModel } from '../context/ModelContext'

export const Effects = observer(() => {
    const model = useModel()

    const handleActivate1 = () => {
        model.activateEffect(0)
    }

    const handleActivate2 = () => {
        model.activateEffect(1)
    }

    return (
        <div className='effects'>
            <ol>
                <li>
                    <EffectSlot effect={model.getEffect(0)} onActivate={handleActivate1} />
                </li>
                <li>
                    <EffectSlot effect={model.getEffect(1)} onActivate={handleActivate2} />
                </li>
            </ol>
        </div>
    )
})

type EffectSlotProps = {
    effect?: Effect
    onActivate: () => void
}

export const EffectSlot = ({ effect, onActivate }: EffectSlotProps) => {

    let svg: ReactNode
    switch (effect) {
        case 'square':
            svg = <SquareEffectSvg />
            break
        case 'switch':
            svg = <SwitchEffectSvg />
            break
        case 'nearest1':
            svg = <NearestEffectSvg variant={1} level={1}/>
            break
        case 'nearest10':
            svg = <NearestEffectSvg variant={10} level={1} />
            break
        case 'nearest100':
            // FIXME level not needed
            svg = <NearestEffectSvg variant={100} level={1} />
            break
        case undefined:
            svg = <NoEffectSvg />
            break
        default:
            return exhaustiveCheck(effect)
    }

    return (
        // biome-ignore lint/a11y/useKeyWithClickEvents: <explanation>
        <div onClick={onActivate} style={{cursor: 'pointer'}}>
            {svg}
        </div>
    )
}
