import { type BoardConfig, getRadii } from './config'
import type { Bed } from '../model/types'
import { polarToCartesian } from '../lib/coords'
import { useLongPress } from '../hooks/useLongPress'
import clsx from 'clsx'

type AnnularSectorProps = {
	className?: string
	config: BoardConfig
	value: number
	type: Bed
	third?: boolean
	onActivate?: (value: number, bed: Bed, alt: boolean) => void
}

export const AnnularSector = ({ className, config, value, type, third = false, onActivate }: AnnularSectorProps) => {
	const handlePress = () => {
		onActivate?.(value, type, false)
	}

	const handleLongPress = () => {
		onActivate?.(value, type, true)
	}

	const longPress = useLongPress({ onPress: handlePress, onLongPress: handleLongPress, longPressDelay: 500 })

	if (value <= 0 || value > 20) {
		return null
	}

	const index = config.scores.findIndex((score) => score === value)
	if (index === -1) {
		return null
	}

	const centerX = 0
	const centerY = 0

	const arcAngle = 360 / config.scores.length
	const arcOffset = -arcAngle / 2
	let startAngle = (index * arcAngle) + arcOffset
	let endAngle = ((index + 1) * arcAngle) + arcOffset

	if (third) {
		startAngle += arcAngle / 3
		endAngle -= arcAngle / 3
	}

	const { inner: innerRadius, outer: outerRadius } = getRadii(config, type)

	const startInner = polarToCartesian(centerX, centerY, innerRadius, startAngle)
	const endInner = polarToCartesian(centerX, centerY, innerRadius, endAngle)
	const startOuter = polarToCartesian(centerX, centerY, outerRadius, startAngle)
	const endOuter = polarToCartesian(centerX, centerY, outerRadius, endAngle)
	const largeArcFlag = endAngle - startAngle <= 180 ? '0' : '1'

	const d = [
		`M ${startInner.x} ${startInner.y}`,
		`A ${innerRadius} ${innerRadius} 0 ${largeArcFlag} 1 ${endInner.x} ${endInner.y}`,
		`L ${endOuter.x} ${endOuter.y}`,
		`A ${outerRadius} ${outerRadius} 0 ${largeArcFlag} 0 ${startOuter.x} ${startOuter.y}`,
		'Z',
	].join(' ')

	return (
		<path
			className={clsx(`segment bed-${type}`, className)}
			d={d}
			{ ...longPress }
		/>
	)
}
