import type Fraction from 'fraction.js'

// really segment type for annular segment
const bedTypes = ['single1', 'treble', 'single2', 'double', 'score', 'modifier'] as const
export type Bed = (typeof bedTypes)[number]

// this is more useful for puzzle logic
export type Stage = Exclude<Bed, 'score' | 'modifier'>

export const stageOrder: Record<Stage, number> = {
    single1: 1,
    treble: 2,
    single2: 3,
    double: 4
}

const operationTypes = ['add', 'sub', 'mul', 'div'] as const
export type Operation = (typeof operationTypes)[number]

export const operationOrder: Record<Operation, number> = {
    add: 1,
    sub: 2,
    mul: 3,
    div: 4,
}

const modifierTypes = ['double', 'half', 'ignore', 'switch'] as const
export type Modifier = (typeof modifierTypes)[number]

export type Action = {
    score: number
    type: Bed
    op?: Operation // will be mandatory, we will get rid of mod
    third?: boolean
    mod?: Modifier
}

export const effectTypes = ['square', 'switch', 'nearest1', 'nearest10', 'nearest100'] as const
export type Effect = (typeof effectTypes)[number]

type SolutionStepCommon = {
    type: 'score' | 'effect'
    stage: Omit<Bed, 'score' | 'modifier'>
    display: string
    result: Fraction
}

export type ScoreSolutionStep = SolutionStepCommon & {
    type: 'score'
    operation: Operation
    modifier?: Modifier
    score: number
    effectiveScore?: Fraction
}

export type EffectSolutionStep = SolutionStepCommon & {
    type: 'effect'
    operation: Operation
    effect: Effect
    effectiveScore: Fraction // Needs to be removed
}

export type SolutionStep = ScoreSolutionStep | EffectSolutionStep
