import type { PropsWithChildren } from 'react'
import { exhaustiveCheck } from '../../lib/exhaustive'
import type { Effect } from '../../model/types'
import { NearestEffect } from './NearestEffect'
import { SquareEffect } from './SquareEffect'
import { SwitchEffect } from './SwitchEffect'

type EffectOverlayProps = PropsWithChildren<{
    effect: Effect | undefined
    level: 1 | 2
}>

export const EffectOverlay = ({ effect, level, children }: EffectOverlayProps) => {
    switch (effect) {
        case 'square':
            return <SquareEffect level={level}>{children}</SquareEffect>
        case 'switch':
            return <SwitchEffect level={level}>{children}</SwitchEffect>
        case 'nearest1':
            return <NearestEffect variant={1} level={level}>{children}</NearestEffect>
        case 'nearest10':
            return <NearestEffect variant={10} level={level}>{children}</NearestEffect>
        case 'nearest100':
            return <NearestEffect variant={100} level={level}>{children}</NearestEffect>
        case undefined:
            return null
        default:
            return exhaustiveCheck(effect)
    }
}
