import type { Bed } from './model/types'
import { ModelProvider } from './context/ModelContext'
import { MODEL } from './model/model'
import { Board } from './board/Board'
import { BOARD_CONFIG } from './board/config'
import { Overlays } from './overlays/Overlays'
import { Effects } from './effects/Effects'
import { ConfigProvider } from './context/ConfigContext'
import { useCallback, useState } from 'react'

import '@fontsource/cinzel'

import './App.css'

import './board.css'
import './effects.css'
// import './solution.css'
import './popup.css'
import Popup from './popup/Popup'

const App: React.FC = () => {
	const [isShowHelp, setShowHelp] = useState(false)

	const handleActivateSector = useCallback((score: number, type: Bed, alt: boolean) => {
		switch (type) {
			case 'single1':
			case 'treble':
			case 'single2':
			case 'double':
			case 'modifier':
				MODEL.activateScore(score, type, alt)
				break
		}
	}, [])

	const handleActivateBull = useCallback(() => MODEL.activateEffectOp(), [])

    const handleClickReset = () => MODEL.reset()

	const handleClickHelp = () => setShowHelp(true)

	const handleClosePopup = () => setShowHelp(false)

	return (
		<ConfigProvider config={BOARD_CONFIG}>
			<ModelProvider model={MODEL}>
				<div className='container'>
					<div className='left-side'>
						<div className='dartboard-container'>
							<Board
								onActivateSector={handleActivateSector}
								onActivateBull={handleActivateBull}
							/>
							<Overlays />
						</div>
						<div className='footer-component'>
							<Effects />
						</div>
					</div>
					{/* <div className='right-side solution' style={{ overflow: 'auto' }}>
						<Solution />
					</div> */}
				</div>
				<div>
					<button type='button' onClick={handleClickReset}>Reset</button>
					<button type='button' onClick={handleClickHelp}>Help</button>
				</div>
				{ isShowHelp && (
					<Popup onClose={handleClosePopup}>
						<h2>Help</h2>
						<p>Click the "beds" to cycle through add, subtract, multiply and divide.</p>
						<p>Click and hold a bed to toggle 1/3 scores.</p>
						<p>Click the bullseye to cycle the operation, add, subtract, multiply or divide, that the bullseye effects apply to.</p>
						<p>Click the boxes below the board to cycle through the bullseye operations, square, reverse digits, round to nearest 1, 10, 100.</p>
						<p>Click the outer ring segments to cycle through double, half, ignore and reverse digits.</p>
						<p>If the board is valid, the solution score will be highlighted.</p>
						<p>Click the reset button to clear the board and start again.</p>
					</Popup>
				)}
			</ModelProvider>
		</ConfigProvider>
	)
}

export default App
