import type { PropsWithChildren } from 'react'
import { useConfig } from '../../context/ConfigContext'
import { exhaustiveCheck } from '../../lib/exhaustive'

// The SVG should be moved to a separate module

type SwitchEffectProps = PropsWithChildren<{
	level: 1 | 2
}>

export const SwitchEffect = ({ level, children }: SwitchEffectProps) => {
	const config = useConfig()

	let size: number
	let stroke: number
	switch (level) {
		case 1:
			size = 5
			stroke = 2
			break
		case 2:
			size = 9
			stroke = 1
			break
		default:
			return exhaustiveCheck(level)
	}

	const effectId = `effect-${level}`
	const clipId = `clip-${level}`

	return (
		<>
			<defs>
				<rect
					id={effectId}
					x={-config.bull + size}
					y={-config.bull + size}
					width={config.bull * 2 - (2 * size)}
					height={config.bull * 2 - (2 * size)}
					stroke='black'
					strokeWidth={stroke}
					fill='none'
					transform='rotate(45 0 0)'
				/>
				<clipPath id={clipId}>
					<use href={`#${effectId}`} />
				</clipPath>
			</defs>

			<use href={`#${effectId}`} />

			<g clipPath={`url(#${clipId})`}>
				{children}
			</g>
		</>
	)
}

export const SwitchEffectSvg = () => {
	const config = useConfig()

	const bull = config.bull; // 240
	const viewBox = `${-bull} ${-bull} ${bull * 2} ${bull * 2}`
	const sideLength = bull * 2 * 0.66
	const offset = sideLength / 2

	return (
	  <svg
		width="64"
		height="64"
		viewBox={viewBox}
		preserveAspectRatio="xMidYMid meet"
	  >
		<rect
		  x={-offset}
		  y={-offset}
		  width={sideLength}
		  height={sideLength}
		  stroke="black"
		  strokeWidth="2"
		  fill="none"
		  transform="rotate(45, 0, 0)"
		/>
	  </svg>
	);
 };
