import type { Effect, Operation } from '../../model/types'
import clsx from 'clsx'
import { EffectOverlay } from './EffectOverlay'
import { useConfig } from '../../context/ConfigContext'

type EffectOverlaysProps = {
	effects: (Effect | undefined)[]
	operation?: Operation
}

export const EffectOverlays = ({ effects, operation }: EffectOverlaysProps) => {
	const config = useConfig()

	// Only one effect may be nearest
	const bothNearest =
		(effects[0] === 'nearest1' || effects[0] === 'nearest10' || effects[0] === 'nearest100') &&
		(effects[1] === 'nearest1' || effects[1] === 'nearest10' || effects[1] === 'nearest100')

	// For simplicity and convenience, do not render the invalid case
	if (bothNearest) {
		return null
	}

	return (
		<>
			<circle className={clsx('segment', operation ?? 'no-operation')} cx={0} cy={0} r={config.bull} />
			<clipPath id='circleClip'>
				<circle cx={0} cy={0} r={config.bull} />
			</clipPath>
			<g clipPath='url(#circleClip)'>
				{ effects.length > 0 && (
					<EffectOverlay effect={effects[effects.length - 1]} level={1}>
						{ effects.length > 1 && (
							<EffectOverlay effect={effects[effects.length - 2]} level={2} />
						)}
					</EffectOverlay>
				)}
			</g>
		</>
	)
}
