import type { Bed } from '../model/types'
import { useConfig } from '../context/ConfigContext'
import { Sector } from './Sector'

type BoardProps = {
	onActivateSector: (score: number, type: Bed, alt: boolean) => void
	onActivateBull: () => void
}

export const Board = ({ onActivateSector, onActivateBull }: BoardProps) => {
	const config = useConfig()

    // The size of the SVG is the radius, plus some space to accomodate the outer border
	const size = (config.radius + config.edgeWidth / 2 + 2) * 2

	return (
		<svg className='dartboard' viewBox={`-${size / 2} -${size / 2} ${size} ${size}`}>
			{config.scores.map((value, index) => (
				<Sector key={value} config={config} index={index} onActivate={onActivateSector} />
			))}

            <circle
                cx={0}
                cy={0}
                r={config.bull}
                className='segment bed-bull'
                onClick={onActivateBull}
				onKeyDown={onActivateBull}
            />

            <circle cx={0} cy={0} r={config.scoreInner} className='numbers-ring-border' />
			<circle cx={0} cy={0} r={config.scoreOuter} className='numbers-ring-border' />

			<circle cx={0} cy={0} r={config.edge} className='border-ring' />
		</svg>
	)
}
