import type { BoardConfig } from '../../board/config'
import { polarToCartesian } from '../../lib/coords'

type IgnoreModifierOverlayProps = {
	startAngle: number
	endAngle: number
	config: BoardConfig
}

// This should be simplified to just straight lines, not curves

export const IgnoreModifierOverlay = ({ config, startAngle, endAngle }: IgnoreModifierOverlayProps) => {
	const segmentAngle = 360 / config.scores.length
	const midRadius = (config.modifierInner + config.modifierOuter) / 2
	const controlRadius = midRadius
	const topLeft = polarToCartesian(0, 0, config.modifierInner, startAngle)
	const bottomRight = polarToCartesian(0, 0, config.modifierOuter, endAngle)
	const topRight = polarToCartesian(0, 0, config.modifierInner, endAngle)
	const bottomLeft = polarToCartesian(0, 0, config.modifierOuter, startAngle)
	const control1 = polarToCartesian(0, 0, controlRadius, startAngle + segmentAngle / 2)
	const control2 = polarToCartesian(0, 0, controlRadius, startAngle + segmentAngle / 2)

	return (
		<g>
			<path
				className='modifier'
				d={`M ${topLeft.x} ${topLeft.y} Q ${control1.x} ${control1.y} ${bottomRight.x} ${bottomRight.y}`}
				strokeWidth='6'
				strokeLinecap='round'
				fill='none'
			/>
			<path
				className='modifier'
				d={`M ${topRight.x} ${topRight.y} Q ${control2.x} ${control2.y} ${bottomLeft.x} ${bottomLeft.y}`}
				strokeWidth='6'
				strokeLinecap='round'
				fill='none'
			/>
		</g>
	)
}
