import { exhaustiveCheck } from '../lib/exhaustive';
import type { Bed } from '../model/types'

export type BoardConfig = {
    radius: number,
    scores: number[],
    bull: number,
    trebleInner: number
    trebleOuter: number,
    doubleInner: number,
    doubleOuter: number,
    scoreInner: number;
    scoreLabel: number;
    scoreOuter: number;
    modifierInner: number;
    modifierOuter: number
    edge: number,
    edgeWidth: number
}

// The commented values are more pleasing for desktop, but to make mobile not totally horrible the values have been tweaked
const newBoardConfig = (radius: number, scores: number[]): BoardConfig => ({
    radius,
    scores,
    bull: radius * 0.057,
    trebleInner: radius * 0.370,
    trebleOuter: radius * 0.440,
    doubleInner: radius * 0.630,
    doubleOuter: radius * 0.708,
    scoreInner: radius * 0.698,
    scoreLabel: radius * 0.785,
    scoreOuter: radius * 0.875,
    modifierInner: radius * 0.875,
    modifierOuter: radius * 1.0,
    edge: radius * 1.0,
    edgeWidth: 4
})

export const BOARD_CONFIG = newBoardConfig(240, [20, 1, 18, 4, 13, 6, 10, 15, 2, 17, 3, 19, 7, 16, 8, 11, 14, 9, 12, 5])

export const getRadii = (config: BoardConfig, type: Bed): { inner: number, outer: number } => {
    switch (type) {
        case 'single1':
            return { inner: config.bull, outer: config.trebleInner }
        case 'treble':
            return { inner: config.trebleInner, outer: config.trebleOuter }
        case 'single2':
            return { inner: config.trebleOuter, outer: config.doubleInner }
        case 'double':
            return { inner: config.doubleInner, outer: config.doubleOuter - 4}
        case 'score':
            return { inner: config.scoreInner, outer: config.scoreOuter }
        case 'modifier':
            return { inner: config.modifierInner, outer: config.modifierOuter }
        default:
            return exhaustiveCheck(type)
    }
}
