import { createContext, type PropsWithChildren, useContext } from 'react'
import type { BoardConfig } from '../board/config'

type ConfigProviderProps = PropsWithChildren<{
	config: BoardConfig
}>

const ConfigContext = createContext<ConfigProviderProps | null>(null)

export const ConfigProvider = ({ config, children }: ConfigProviderProps) => {
	return <ConfigContext.Provider value={{ config }}>{children}</ConfigContext.Provider>
}

export const useConfig = (): BoardConfig => {
	const context = useContext(ConfigContext)
	if (!context) {
		throw new Error('useConfig must be used within a ConfigProvider')
	}
	return context.config
}
