import type { Bed } from '../model/types'
import type { BoardConfig } from './config'
import { polarToCartesian } from '../lib/coords'
import { AnnularSector } from './AnnularSector'

type SectorProps = {
    config: BoardConfig,
    index: number
    onActivate: (score: number, type: Bed, alt: boolean) => void
}

/**
 * One complete sector (pizza-slice) of the board.
 *
 * A sector contains all of the scoring beds (two single beds, double, and treble), the score
 * label, and outer modifier.
 */
export const Sector = ({ config, index, onActivate }: SectorProps) => {
    const arcAngle = 360 / config.scores.length
	const arcOffset = -arcAngle / 2

    const startAngle = index * arcAngle + arcOffset
    const midAngle = index * arcAngle + arcOffset + arcAngle / 2
    const endAngle = (index + 1) * arcAngle + arcOffset

    const { x, y } = polarToCartesian(0, 0, config.scoreLabel, midAngle)

    const value = config.scores[index]

    return (
        <>
            <AnnularSector
                key={`numbers-ring-${value}`}
                config={config}
                value={value}
                type='score'
                onActivate={() => onActivate(value, 'score', false)}
            />

            {/* biome-ignore lint/a11y/useKeyWithClickEvents: <explanation> */}
            <text
                key={`label-${value}`}
                className='score-text'
                x={x}
                y={y}
                textAnchor='middle'
                dominantBaseline='central'
                onClick={() => onActivate(value, 'score', false)}
            >
                {value}
            </text>

            <g key={value} className={`segment-group segment-group-${index % 2 === 0 ? 'odd' : 'even'}`}>
                {/* Inner Single */}
                <AnnularSector
                    config={config}
                    value={value}
                    type='single1'
                    onActivate={onActivate}
                />
                {/* Triple */}
                <AnnularSector
                    config={config}
                    value={value}
                    type='treble'
                    onActivate={onActivate}
                />
                {/* Outer Single */}
                <AnnularSector
                    config={config}
                    value={value}
                    type='single2'
                    onActivate={onActivate}
                />
                {/* Double */}
                <AnnularSector
                    config={config}
                    value={value}
                    type='double'
                    onActivate={onActivate}
                />
                {/* Outer ring */}
                <AnnularSector
                    config={config}
                    value={value}
                    type='modifier'
                    onActivate={onActivate}
                />

                {/* Outer ring radial strokes */}
                <line
                    x1={polarToCartesian(0, 0, config.modifierInner + 0.5, startAngle).x}
                    y1={polarToCartesian(0, 0, config.modifierInner + 0.5, startAngle).y}
                    x2={polarToCartesian(0, 0, config.modifierOuter - 0.5, startAngle).x}
                    y2={polarToCartesian(0, 0, config.modifierOuter - 0.5, startAngle).y}
                    className='outer-ring-radial'
                />
                <line
                    x1={polarToCartesian(0, 0, config.modifierInner + 0.5, endAngle).x}
                    y1={polarToCartesian(0, 0, config.modifierInner + 0.5, endAngle).y}
                    x2={polarToCartesian(0, 0, config.modifierOuter - 0.5, endAngle).x}
                    y2={polarToCartesian(0, 0, config.modifierOuter - 0.5, endAngle).y}
                    className='outer-ring-radial'
                />
            </g>
        </>
    )
}
