import type { Modifier, Operation, Effect } from './types'
import { exhaustiveCheck } from '../lib/exhaustive'

export const getNextOperation = (op?: Operation): Operation | undefined => {
    switch (op) {
        case undefined:
            return 'add'
        case 'add':
            return 'sub'
        case 'sub':
            return 'mul'
        case 'mul':
            return 'div'
        case 'div':
            return undefined
        default:
            return exhaustiveCheck(op)
    }
}

export const getNextModifier = (mod?: Modifier): Modifier | undefined => {
    switch (mod) {
        case undefined:
            return 'double'
        case 'double':
            return 'half'
        case 'half':
            return 'ignore'
        case 'ignore':
            return 'switch'
        case 'switch':
            return undefined
        default:
            return exhaustiveCheck(mod)
    }
}

export const getNextEffect = (effect: Effect | undefined) => {
    switch (effect) {
        case 'square':
            return 'switch'
        case 'switch':
            return 'nearest1'
        case 'nearest1':
            return 'nearest10'
        case 'nearest10':
            return 'nearest100'
        case 'nearest100':
            return undefined
        case undefined:
            return 'square'
        default:
            throw exhaustiveCheck(effect)
    }
}
