import type { OperationAction } from './model'
import { type Operation, operationOrder, type Stage, stageOrder } from './types'

export const sorter = (effectOp?: Operation) => (a: OperationAction, b: OperationAction) => {
	const stageDiff  = stageOrder[a.type as Stage] - stageOrder[b.type as Stage] // FIXME no casts
	if (stageDiff !== 0) {
		return stageDiff
	}

	if (a.op === effectOp && b.op !== effectOp) {
		return 1
	}

	if (b.op === effectOp && a.op !== effectOp) {
		return -1
	}

	const orderDiff = operationOrder[a.op] - operationOrder[b.op]
	if (orderDiff !== 0) {
		return orderDiff
	}

	return b.score - a.score
}
