import Fraction from 'fraction.js'

export const switchDigits = (value: Fraction) => {
    try {
        const isNegative = value.s < 0
        const reversed = value.abs().toString().split('').reverse().join('')
        let result = new Fraction(reversed)
        if (isNegative) {
            result = result.mul(-1)
        }
        return result
    } catch (error) {
        // We can't reverse, for example, 39.1(3) for 39.1333..., so just return the original value
        console.error('Switch digits failed for value:', value.toString(), value.toFraction(), value.valueOf())
        return value
    }
}
