import type { PropsWithChildren } from 'react'
import { useConfig } from '../../context/ConfigContext'
import { exhaustiveCheck } from '../../lib/exhaustive'

// The SVG should be moved to a separate module

type SquareEffectProps = PropsWithChildren<{
	level: 1 | 2
}>

export const SquareEffect = ({ level, children }: SquareEffectProps) => {
	const config = useConfig()

	let size: number
	let stroke: number
	switch (level) {
		case 1:
			size = 5
			stroke = 2
			break
		case 2:
			size = 9
			stroke = 1
			break
		default:
			return exhaustiveCheck(level)
	}

	const effectId = `effect-${level}`
	const clipId = `clip-${level}`

	return (
		<>
			<defs>
				<rect
					id={effectId}
					x={-config.bull + size}
					y={-config.bull + size}
					width={config.bull * 2 - size * 2}
					height={config.bull * 2 - size * 2}
					stroke='black'
					strokeWidth={stroke}
					fill='none'
				/>
				<clipPath id={clipId}>
					<use href={`#${effectId}`} />
				</clipPath>
			</defs>

			<use href={`#${effectId}`} />

			<g clipPath={`url(#${clipId})`}>
				{children}
			</g>
		</>
	)
}

export const SquareEffectSvg = () => {
	const config = useConfig()

	const bull = config.bull // 240
	const viewBox = `${-bull} ${-bull} ${bull * 2} ${bull * 2}`

	return (
		<svg width='64' height='64' viewBox={viewBox} preserveAspectRatio='xMidYMid meet'>
			<rect
				x={-bull + 1}
				y={-bull + 1}
				width={bull * 2 - 2}
				height={bull * 2 - 2}
				stroke='black'
				strokeWidth='2'
				fill='none'
			/>
		</svg>
	)
}
